import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { ApiProvider } from '../../providers/api/api'
/**
 * Generated class for the SimulacaoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-simulacao',
  templateUrl: 'simulacao.html',
})
export class SimulacaoPage {

  valor; data; cdi;

  constructor(public navCtrl: NavController, public navParams: NavParams, public api: ApiProvider) {
    this.valor = this.navParams.get('valor')
    this.data = this.navParams.get('data')
    this.cdi = this.navParams.get('cdi')

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SimulacaoPage');
  }

}



